<template>
  <div class="slider-wrapper">
    <div class="slider">
      <sliderItem
        v-for="(item, index) in slider_data"
        :key="index"
        :item_data="item"
        :class="{ centered: index === 1 }"
      />
    </div>
    <div class="nav">
      <svg
        @click="previousSlide"
        width="28"
        height="48"
        viewBox="0 0 7 12"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M7 10.58L2.9925 6L7 1.41L5.76625 0L0.51625 6L5.76625 12L7 10.58Z"
          fill="#CCAE68"
        />
      </svg>
      <svg
        @click="nextSlide"
        width="28"
        height="48"
        viewBox="0 0 7 12"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M0.516357 10.58L4.52386 6L0.516357 1.41L1.75011 0L7.00011 6L1.75011 12L0.516357 10.58Z"
          fill="#CCAE68"
        />
      </svg>
    </div>
  </div>
</template>

<script>
import sliderItem from "./slider-item";

export default {
  components: {
    sliderItem,
  },
  props: ["slider_data"],
  methods: {
    nextSlide() {
      if (this.slider_data.length > 3) {
        this.slider_data.push(this.slider_data.shift());
      }
    },
    previousSlide() {
      if (this.slider_data.length > 3) {
        this.slider_data.unshift(this.slider_data.pop());
      }
      this.slider_data.forEach((element) => {
        console.log(element.name);
      });
    },
  },
};
</script>
